  content: "";
  position: absolute;
  display: block;
  top: 100%;
  background-color: inherit;
  width: 1.75rem;
  height: 1.75rem;
  clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
}
.speech-bubble.speech-bubble-sm:before {
  width: 1.25rem;
  height: 1.25rem;
}
.speech-bubble.speech-bubble-lg:before {
  width: 2.5rem;
  height: 2.5rem;
}

